import fetch from 'isomorphic-fetch'
import RetinaXService from "./RetinaXService";

export default class CellTypeService extends RetinaXService{

    createCellType(createCellTypeRequest){
        let url = this.baseUrl + "/build/createCellType/";

        return fetch(url,{
            method: 'POST',
            body: JSON.stringify(createCellTypeRequest),
            headers: this.headers
        }).then(response => {
            console.log(response);
            return response.json();
        }).then(body => {
            console.log(body);
            // alert("Cell Type " + JSON.stringify(body) + "\ncreated successfully");
            return body;
        }).catch((reason => alert(reason)));
    }

    getCellTypes(){
        let url = this.baseUrl + "/build/cellTypes";

        return fetch(url,{
            method: 'GET',
            headers: this.headers
        }).then(response => {
          //  console.log(response);
            return response.json();
        }).then(body => {
            //console.log(body);
            // alert("Cell Type " + JSON.stringify(body) + "\ncreated successfully");
            return body;
        }).catch((reason => alert(reason)));
    }

    deleteCellType(id){
        let url = this.baseUrl + "/build/cellTypes/"+id;

        return fetch(url,{
            method: 'DELETE',
            headers: this.headers
        }).catch((reason => alert(reason)));
    }
}