import React from 'react'
import './OutputCellsList.css'
import {Close} from 'grommet-icons'
import { ListGroup, ListGroupItem, ListGroupItemHeading, ListGroupItemText } from 'reactstrap';

export default class OutputCellsList extends React.Component {

    render() {
        const outputCells = this.props.outputCells;

        return (
            <ListGroup className={"listGroup"} flush>
                {outputCells.map(outputCell => (
                    <ListGroupItem key={outputCell.instance.id} className={"outputCell"}>
                       <div className={"removeButton"}>
                        <Close/>
                       </div>
                        <ListGroupItemHeading className={"outputCellHeader"}>
                            {outputCell.instance.cellType.name}

                        </ListGroupItemHeading>
                        {/*<ListGroupItemText></ListGroupItemText>*/}
                        <small>{outputCell.instance.id}</small>
                    </ListGroupItem>
                    )
                )
                }
            </ListGroup>
        );
    }
}