import React from "react";
import "./CreateCellTypeForm.css"
import 'katex/dist/katex.min.css';

import { BlockMath} from 'react-katex';
import {Button, ButtonGroup, Label, Input, FormGroup, CardTitle} from 'reactstrap';
import CellTypeService from "../../services/CellTypeService";



export class CreateCellTypeForm extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            name: "",
            transformType: "",
            function:{
                expression: "",
                variables:[]
            },
        };
        this.props = props;
        this.service = new CellTypeService();
        this.toggle = props.toggle;
        this.addCellType = props.addCellType;
    }

    handleNameChange(event) {
        this.setState({name: event.target.value});
    }


    handleExpressionChange(event) {

        let _function = this.state.function;
        _function.expression = event.target.value;

        this.setState({function: _function});
    }

    handleVariablesChange(event) {

        let _function = this.state.function;
        _function.variables = event.target.value.split(',');

        this.setState({function: _function});
    }

    handleTransformChange(transform) {
        this.setState({transformType: transform});
    }

    handleSubmit(event) {
        event.preventDefault();

        this.service.createCellType({
            name: this.state.name,
            transformType: this.state.transformType.toUpperCase().split(" ").join("_"),
            createFunctionRequest: this.state.function
        }).then((cellType) => {
            this.addCellType(cellType);
            this.toggle();
        });

        return event;
    }

    getExpressionIfDefined(){
        if(this.state.function.expression){
            return this.state.function.expression;
        }
        return " ";
    }


    isActiveTransformType(transformType){
        return transformType == this.state.transformType;
    }

    render(){
        let transforms = ["Analog to Analog", "Analog to Digital", "Digital to Analog", "Digital to Digital"];

        console.log(this.props);
        return(
            <div id="createCellForm">
                <form onSubmit={this.handleSubmit.bind(this)}>
                    <CardTitle className={"title"}>Create A New Cell Type</CardTitle>
                    <CreateCellTypeFormTextRow text="Name:" value={this.state.name}  placeholder="Example: A" onChange={this.handleNameChange.bind(this)}/>

                    <CreateCellTypeFormTextRow text="Function Expression:"  value={this.state.function.expression}  placeholder="x + y * z" onChange={this.handleExpressionChange.bind(this)}/>

                    <CreateCellTypeFormTextRow text="Variables:" value={this.state.function.variables} placeholder="x, y, z" onChange={this.handleVariablesChange.bind(this)}/>


                    <FormGroup>
                    <label id={"transformTypeLabel"} className="col-sm-4 col-form-label">Transform Type:</label>

                    <ButtonGroup>
                        {transforms.map((transfrom) =>
                            <Button active={this.isActiveTransformType(transfrom)} onClick={(event) => this.handleTransformChange(transfrom)}>
                                {transfrom}
                            </Button>)}
                    </ButtonGroup>
                    </FormGroup>

                    <div id ={"blockMath"} className="form-group row">
                        <div className={"col-sm-10"}>
                            <BlockMath
                                math={this.getExpressionIfDefined()}
                                errorColor={'#cc0000'}
                                renderError={(error) => {
                                return <b>Illegal Expression</b>}}
                            />
                        </div>
                    </div>

                    <button className="btn btn-dark btn-lg btn-block col-sm-11" type="submit">Create Cell Type</button>
                </form>
            </div>
        );
    }



}

export class CreateCellTypeFormTextRow extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            text: props.text,
            placeholder: props.placeholder,
            onChange: props.onChange,
            value: props.value
        };
    }

    render(){
      return(
          <div>
              <FormGroup>
                <Label className={"col-form-label"}> {this.state.text} </Label>
                <Input className={"textField"} placeholder={this.state.placeholder} type="text" onChange={this.state.onChange} />
              </FormGroup>
          </div>
      );
    };
}