import React, { Component } from 'react';
import './NetworkBuilder.css'

import CellTypePicker from "./CellTypePicker/CellTypePicker";
import Graph from "./UberGraphView/fromUberGit/graph.js";
import {EMPTY_TYPE, SPECIAL_TYPE} from "./UberGraphView/fromUberGit/graph-config";
import VerticalLinearStepper from "../SimulationUI/Stepper/VerticalLinearStepper";
import ResultsModal from "../SimulationUI/ResultsModal/ResultsModal";
import {Alert} from "reactstrap";
import SimulateNetworkService from "../../services/SimulateNetworkService";

export const NONE = -1, SELECTING_OUTPUT_CELLS = 0, UPLOADING_INPUT_FILE = 1, SHOWING_RESULTS = 2;

class NetworkBuilder extends Component{

    constructor(){
        super();
        this.state = {
            selectedType: null,
            simulationMode: false,
            selectedOutputCells: [],
            activeStep: NONE,
            showResults: false,
            outputResults:null,
            maxTime:null,
            networkIsValid: false
        };

        this.simulateService = new SimulateNetworkService();

    }

    setSelectedCellType(cellType){
        let viewCellType = cellType;
        if(cellType.transformType !== 'INPUT_TO_ANALOG'){
            viewCellType.view = EMPTY_TYPE;
        }else{
            viewCellType.view = SPECIAL_TYPE;
        }

        this.setState({selectedType: viewCellType});
    }

    onSimulateNetworkClicked(){
        this.setState({simulationMode: !this.state.simulationMode, activeStep:   SELECTING_OUTPUT_CELLS});
    }

    onOutputCellSelected(outputCell){
        let selectedOutputCells = this.state.selectedOutputCells;
        if(selectedOutputCells.filter(cell => cell.instance.id === outputCell.instance.id).length === 0) {
            selectedOutputCells.push(outputCell);
            this.setState({selectedOutputCells});
        }
    }

    handleNext = () => {

        this.setState(state => ({
            activeStep: state.activeStep + 1,
        }));
    };

    handleBack = () => {
        this.setState(state => ({
            activeStep: state.activeStep - 1,
        }));
    };

    handleReset = () => {
        this.setState({
            activeStep: 0,
        });
    };

    toggleShowResults(){
        this.setState({
            showResults: !this.state.showResults
        });
    }

    onResultsArrived(outputResults, maxTime){
        this.setState({outputResults:outputResults.cellsResults, maxTime:maxTime, showResults: true});
    }

    componentDidMount(){
        this.refreshNetworkValidation();
    }

    refreshNetworkValidation(){
        this.simulateService.isNetworkStructureValid().then(response => {
            this.setState({networkIsValid: Boolean(response.value)});
        });
    }

    render(){
        let buttonText = this.state.simulationMode ?  "" : "Simulate Network";

        let sideComponent = !this.state.simulationMode ?
            <CellTypePicker className={"picker"} setSelectedType={this.setSelectedCellType.bind(this)}/>
            : <VerticalLinearStepper
                className={"picker"}
                activeStep={this.state.activeStep}
                handleNext={this.handleNext.bind(this)}
                handleBack={this.handleBack.bind(this)}
                handleReset={this.handleReset.bind(this)}
                outputCells={this.state.selectedOutputCells}
                onResultsArrived={this.onResultsArrived.bind(this)}
            />;

        let topView;
        if(this.state.networkIsValid){
             topView = (<span>
                    <button className={"simulateNetworkButton btn btn-secondary"} hidden={this.state.simulationMode} onClick={this.onSimulateNetworkClicked.bind(this)}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="7" height="8" viewBox="0 0 7 8"
                             className="primary">
                            <polygon fill="#FFFFFF" fillRule="evenodd" points="13 10 13 18 20 14"
                                     transform="translate(-13 -10)"/>
                        </svg> &nbsp;{buttonText}&nbsp;
                    </button>

                {this.state.simulationMode &&
                <button className={"simulateNetworkButton btn btn-secondary"}
                        onClick={() => this.setState({simulationMode:false})}> Cancel </button>}
                </span>);
        }else{
            topView =  (<Alert color="warning">
                The current network structure is not yet valid for simulation
            </Alert>)
        }

        return (
            <div className={"networkBuilder"}>
                {topView}

                <div className={"networkBuilderBody"}>
                        {sideComponent}
                        <Graph className={"graphView"}
                               selectedType={this.state.selectedType}
                               simulationMode={this.state.simulationMode}
                               simulationStep={this.state.activeStep}
                               onOutputCellSelected={this.onOutputCellSelected.bind(this)}
                               onAddCellOrConnectCells={this.refreshNetworkValidation.bind(this)}
                        />
                </div>



                <ResultsModal toggle={this.toggleShowResults.bind(this)}
                              isOpen={this.state.showResults}
                              outputCells={this.state.selectedOutputCells}
                              outputResults={this.state.outputResults}
                              maxTime={this.state.maxTime}/>


            </div>
        );
    }
}

export default NetworkBuilder;