import React from 'react'
import {Button, Modal, ModalBody, ModalFooter, ModalHeader, Form, FormGroup, Label, Col, Input, Dropdown, DropdownItem, DropdownMenu, DropdownToggle} from "reactstrap";
import CellInstanceService from "../../services/CellInstanceService";

export default class ConnectForm extends React.Component {

    constructor(props){
        super(props);

        this.state = {
            dropdownOpen: false,
            selected: undefined,
            delay: 0
        };

        this.toggle = props.toggle;
        this.dropToggle = this.dropToggle.bind(this);
        this.srcCell = props.sourceCell;
        this.destCell = props.destCell;
        this.service = new CellInstanceService();
        this.onConnectCells = props.onConnectCells;
    }

    dropToggle() {
        this.setState(prevState => ({
            dropdownOpen: !prevState.dropdownOpen
        }));
    }

    submit(){
        this.service.connectCells(this.srcCell.instance, this.destCell.instance, this.state.delay, this.state.selected);
        this.onConnectCells(this.srcCell, this.destCell, this.state.selected, this.state.delay);
        this.toggle();
    }

    render() {

        let variables = this.destCell.instance.cellType.function.variables.map((variable) => variable.variableName);

        /*
        {JSON.stringify(this.srcCell.instance)} to {JSON.stringify(this.destCell.instance)}
         */

        return (
            <div>
                <ModalHeader toggle={this.toggle}>Connect Cells</ModalHeader>
                <ModalBody>
                    <Form>
                        <FormGroup row>
                            <Label for="delay" sm={2}>Delay:</Label>
                            <Col sm={10}>
                                <Input type="number" pattern="[0-9]*" min="0" name="delay" id="delay" placeholder="0, 1, 2, 3..." value={this.state.delay} onChange={(event) => this.setState({delay: event.target.value})}/>
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Label for="variable" sm={6}>Destination Variable:</Label>
                            <Col sm={6}>
                                <Dropdown isOpen={this.state.dropdownOpen} toggle={this.dropToggle}>
                                    <DropdownToggle caret>
                                        {this.state.selected ? this.state.selected : "Select Variable"}
                                    </DropdownToggle>
                                    <DropdownMenu className={"variablePicker"}>
                                        <DropdownItem header>Choose one:</DropdownItem>
                                        {variables.map((variable, index) =>{
                                            return <DropdownItem key={index} onClick={() => this.setState({selected: variable})}>{variable} </DropdownItem>;
                                        })}
                                    </DropdownMenu>
                                </Dropdown>
                            </Col>
                        </FormGroup>
                    </Form>
                </ModalBody>
                <ModalFooter>
                    <Button color="primary" onClick={this.submit.bind(this)} disabled={!this.state.selected || !this.state.delay}>Connect</Button>{' '}
                    <Button color="secondary" onClick={this.toggle}>Cancel</Button>
                </ModalFooter>

            </div>
        );
    }
}