import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepContent from '@material-ui/core/StepContent';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { CSVLink, CSVDownload } from "react-csv";
import Papa from 'papaparse';

import './VerticalLinearStepper.css'

import OutputCellsList from "../OutputCellsList/OutputCellsList";
import {SELECTING_OUTPUT_CELLS, SHOWING_RESULTS, UPLOADING_INPUT_FILE} from "../../NetworkBuilderUI/NetworkBuilder";
import SimulateNetworkService from "../../../services/SimulateNetworkService";
import CellInstanceService from "../../../services/CellInstanceService";


const styles = theme => ({
    root: {
        width: '90%',
    },
    button: {
        marginTop: theme.spacing.unit,
        marginRight: theme.spacing.unit,
    },
    actionsContainer: {
        marginBottom: theme.spacing.unit * 2,
    },
    resetContainer: {
        padding: theme.spacing.unit * 3,
    },
});

function getSteps() {
    return ['Select Output Cells', 'Download Input Data File Format', 'Upload Filled Input File'];
}

class VerticalLinearStepper extends React.Component {


    constructor(props){
        super(props);

        this.state = {
            csvHeaders: null
        }
    }

    componentDidMount(){
        let service = new CellInstanceService();
        service.getInputCells().then(inputCells => {
            const csvHeaders = inputCells.map(inputCell => "" + inputCell.id);
            this.setState({csvHeaders})
        });

    }

    transpose(array){
        return array[0].map((col, i) => array.map(row => row[i]));
    }

    handleFileUpload(file){
        let fileReader = new FileReader();

        fileReader.onloadend = () => {
            let content = fileReader.result;
            let result = Papa.parse(content);


            if(result.errors.length !== 0){
                alert(result.errors.map(err => "Message:  " + err.message  + "\nRow:  " + err.row));
            }
            else{
                this.inputData = this.transpose(result.data);
            }

        };

        fileReader.readAsText(file);
    }

    getSelectingOutputCellsView(){
        const outputCells = this.props.outputCells;
        return (
            <div>
                <Typography>Click on the cells that you want to see their calculations output over time.</Typography>
                <OutputCellsList outputCells={outputCells}/>
            </div>);
    }

    getUploadingInputFileView(){
        return (
            <div>
                <Typography>Download the following file and fill it with your data:</Typography>
                <CSVLink filename={"RetinaX_Input_File.csv"} data={[this.state.csvHeaders]}>Click Here</CSVLink>
            </div>
        );
    }

    getShowingResultsView(){
        return (
            <div>
                <Typography>Upload your input data file here:</Typography>
                <input type={"file"}
                       accept={".csv"}
                       style={{fontSize: "15px"}}
                       onChange={(event) => this.handleFileUpload(event.target.files[0])}
                />
            </div>
        );
    }
    getStepContent(step) {
        switch (step) {
            case SELECTING_OUTPUT_CELLS:
                return this.getSelectingOutputCellsView();

            case UPLOADING_INPUT_FILE:
                return this.getUploadingInputFileView();

            case SHOWING_RESULTS:
                return this.getShowingResultsView();

            default:
                return 'Unknown step';
        }
    }

    simulateNetwork(){
        let service = new SimulateNetworkService();
        let outputInstances = this.props.outputCells.map(outputCell => outputCell.instance);
        let userInput = {};


        this.inputData.forEach(row => {
            userInput[row[0]] =  row.slice(1, row.length);
        });

        let maxTime = this.inputData[0].length - 1;
        service.simulateNetwork(outputInstances, userInput, maxTime)
            .then(results => {
                // alert("RESULTS:\n" + JSON.stringify(results));


                this.props.onResultsArrived(results, maxTime);
            });
    }

    render() {
        const { classes, activeStep, handleBack, handleNext, handleReset} = this.props;
        const steps = getSteps();

        return (
            <div className={classes.root + " stepper-container"}>
                <h2 className={"pickerTitle"}>Simulation</h2>

                <Stepper className={"stepper"} activeStep={activeStep} orientation="vertical">
                    {steps.map((label, index) => (
                        <Step key={label}>
                            <StepLabel>{label}</StepLabel>
                            <StepContent>
                                {this.getStepContent(index)}
                                <div className={classes.actionsContainer}>
                                    <div>
                                        <Button
                                            disabled={activeStep === 0}
                                            onClick={handleBack}
                                            className={classes.button}
                                        >
                                            Back
                                        </Button>
                                        <Button
                                            variant="contained"
                                            onClick={() =>{
                                                 if(activeStep === SHOWING_RESULTS) {
                                                    console.log("simulateNetwork()");
                                                    this.simulateNetwork();
                                                }
                                                console.log("next()");
                                                handleNext();
                                            }}
                                            color={"primary"}
                                            className={"stepperButton"}
                                            disabled={this.props.outputCells.length === 0}
                                        >
                                            {activeStep === steps.length - 1 ? 'Simulate' : 'Next'}
                                        </Button>
                                    </div>
                                </div>
                            </StepContent>
                        </Step>
                    ))}
                </Stepper>
                {activeStep === steps.length && (
                    <Paper square elevation={0} className={classes.resetContainer}>
                        <Typography>All steps completed - you&apos;re finished</Typography>
                        <Button onClick={handleReset} className={classes.button}>
                            Reset
                        </Button>
                    </Paper>
                )}

            </div>
        );
    }
}

VerticalLinearStepper.propTypes = {
    classes: PropTypes.object,
};

export default withStyles(styles)(VerticalLinearStepper);