// import { Table } from 'reactstrap';
import { Table } from 'antd';

import React from 'react'
import "antd/dist/antd.css";

export default class ResultsTable extends React.Component{

    render() {
        const {outputCells, outputResults, maxTime} = this.props;

        let ids =[{title: "t", dataIndex: "t", fixed: 'left', width: 60}];
        outputCells.forEach(outputCell => ids.push({
            title: outputCell.id + " (" + outputCell.instance.cellType.name +")",
            dataIndex: outputCell.id.toString(),
            width: 200,
            key: outputCell.id
        }));

        let tableContent = [];
        for (let i = 0; i < maxTime; i++) {
            let timePoint = {};
            ids.forEach(id => {
                if(id.dataIndex == "t"){
                    timePoint[id.dataIndex] = i;
                }else {
                    timePoint[id.dataIndex] = outputResults[id.dataIndex][i].value;
                }
                timePoint.key = i ;
            });
            tableContent.push(timePoint);
        }
        return (
            <div>
                <Table columns={ids} dataSource={tableContent} bordered={true} size={"middle"} useFixedHeader={true} pagination={{ pageSize: 10 }} scroll={{ y: 240, x:500}}/>
            </div>
        )
    }
}

