import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import './CellTypePicker.css'

import { ListGroup, ListGroupItem, Button, Modal} from 'reactstrap';
import {CreateCellTypeForm} from "../../CellTypeCreatorUI/CreateCellTypeForm";

import Draggable from 'react-draggable';
import CellTypeService from "../../../services/CellTypeService";
import Circle from "../UberGraphView/components/circle";
// import Node from "../UberGraphView/components/node"; // The default
import {EmptyNodeShape} from '../UberGraphView/fromUberGit/graph-config.js';
import {CSVLink} from "react-csv";

export default class CellTypePicker extends Component{

    constructor(props){
        super(props);
        this.state = {
            modal: false,
            dragged:0,
            cellTypes: [],
            selectedCellType: {},
            setSelectedType : props.setSelectedType
        };
        this.service = new CellTypeService();
        this.toggle = this.toggle.bind(this);
    }

    onCreateCellTypeClicked(event){
        this.toggle();
    }

    addCellType(cellType){
        let types = this.state.cellTypes;
        types.push(cellType);

        this.setState({
            cellTypes: types
        });
    }

    toggle() {
        this.setState({
            modal: !this.state.modal
        });
    }

    onStart() {
        this.setState({dragged:1});
    }

    onStop() {
        this.setState({dragged:0});
    }

    select(type){
        this.setState({selectedCellType:type});
        this.state.setSelectedType(type);
    }

    isSelected(type){
        return this.state.selectedCellType.id === type.id;
    }
    componentWillMount(){
        this.service.getCellTypes().then(types => {
            this.setState({cellTypes: types});
        });
    }

    render(){
        const dragHandlers = {onStart: this.onStart.bind(this), onStop: this.onStop.bind(this)};
        let types = this.state.cellTypes;
//float-right
        return(
            <div className={'cellTypePicker'}>

                <h2 className={"pickerTitle"}>Cell Types</h2>

                <ListGroup className={'cellTypesList border border-dark'}>
                    {types.map((type, index) => {
                            let id = "cellType"+index;
                            return (<ListGroupItem key={index} active={this.isSelected(type)} onClick={() => this.select(type)}>
                                    {/*<img className={"deleteCellTypeButton"} src={"/images/multiply.svg"} onClick={() => this.service.deleteCellType(type.id)}/>*/}
                                    <div className={"cellType"} id={id} key={index}>
                                        <h6>{type.name}</h6>
                                        <br/>
                                        <small>{type.function.expression}</small>
                                        <br/>
                                        <small>{type.transformType.replace('_TO_','  ')}</small>
                                    </div>
                            </ListGroupItem>);
                        }
                    )}
                </ListGroup>


                <Modal style={{borderRadius:"4rem"}} isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
                    <CreateCellTypeForm toggle = {this.toggle} addCellType={this.addCellType.bind(this)}/>
                </Modal>
                <hr/>
                <Button className={"createCellTypeBtn"} onClick={this.onCreateCellTypeClicked.bind(this)}>
                    <svg width="18" height="22" xmlns="http://www.w3.org/2000/svg">
                        <path className="primary" d="M8 2v6H2v2h6v6h2v-6h6V8h-6V2H8z" fill="#ffffff"/>
                    </svg>
                    &nbsp; Create a New Cell Type
                </Button>
            </div>
        );
    }
}
