import fetch from 'isomorphic-fetch'
import RetinaXService from "./RetinaXService";
export default class SimulateNetworkService extends RetinaXService{

    isNetworkStructureValid(){
        let url = this.baseUrl + "/simulate/isNetworkValid/";

        return fetch(url, {
            method: 'GET',
            headers: this.headers
        }).then(response => {
            console.log(response);
            return response.json();
        }).then(body =>{
            console.log(body);
            return body;
        }).catch((reason => alert(reason)));
    }

    simulateNetwork(outputCells, userInput, maxTime){
        let url = this.baseUrl + "/simulate";

        let headers = this.headers;
        console.log(url);

        return fetch(url, {
            method:'POST',
            body: JSON.stringify({
                outputCells: outputCells,
                userInput: userInput,
                maxTime: maxTime
            }),
            headers: headers
        }).then(response => {
            console.log(response);
            return response.json();
        }).then(body => {
            console.log(body);
            return body;
        }).catch((reason => alert(reason)));
    }
}