import React from 'react'
import {Modal, ModalBody, ModalFooter, ModalHeader} from 'reactstrap'
import ResultsTable from "../ResultsTable/ResultsTable";
import './ResultsModal.css'
import {CSVLink} from "react-csv";

export default class ResultsModal extends React.Component {

    render() {
        const {isOpen, toggle, outputCells, outputResults, maxTime} = this.props;

        let headers = outputCells.map(outputCell => outputCell.id);
        let tableContent = [];
         for (let i = 0; i < maxTime; i++) {
             let row = [];
             headers.forEach(id => {
                 console.log("outputResults: " + JSON.stringify(outputResults));
                 row.push(outputResults[id.toString()][i].value);
             });
             tableContent.push(row);
         }
        return (
            <Modal  className={"resultsModal"} isOpen={isOpen} toggle={toggle}>
                <ModalHeader className={"resultsModal"}>Results:</ModalHeader>
                <ModalBody className={"resultsModal"}>
                    <ResultsTable outputCells={outputCells}
                                         outputResults={outputResults}
                                         maxTime={maxTime}/>
                </ModalBody>
                <ModalFooter className={"resultsModal"}>
                    <CSVLink className={"btn btn-success"} filename={"RetinaX_Results_File.csv"} headers={headers} data={tableContent}>
                        <img src={"/images/csv.svg"}/>
                        &nbsp;
                        Download as CSV</CSVLink>
                </ModalFooter>
            </Modal>
        );
    }
}