import fetch from 'isomorphic-fetch'
import RetinaXService from "./RetinaXService";

export default class CellInstanceService extends RetinaXService{

    constructor() {
        super();
    }

    addCellInstance(cellType, x, y) {
        let url = this.baseUrl + "/build/addCell/";


        return fetch(url, {
            method: 'POST',
            body: JSON.stringify({
                cellTypeId: cellType.id,
                x: x,
                y: y
            }),
            headers: this.headers
        }).then(response => {
            console.log(response);
            return response.json();
        }).then(body => {
            console.log(body);
            return body;
        }).catch((reason => alert(reason)));
    }

    connectCells(sourceCell, destinationCell, delay, variable){
        let url = this.baseUrl + "/build/connect/";


        return fetch(url, {
            method: 'POST',
            body: JSON.stringify({
                sourceCell: sourceCell,
                destinationCell: destinationCell,
                delay: delay,
                inputFunctionVariable: variable
            }),
            headers: this.headers
        }).then(response => {
            //console.log(response);
            return response.json();
        }).then(body => {
            //console.log(body);
            return body;
        }).catch((reason => alert(reason)));
    }

    areCompatible(sourceCell, destinationCell){
        let url = this.baseUrl + "/build/areCompatible?srcTransform="+sourceCell.cellType.transformType
            +"&destTransform=" + destinationCell.cellType.transformType;

        return fetch(url, {
            method: 'GET',
            headers: this.headers
        }).then(response => {
            //console.log(response);
            return response.json();
        }).then(body =>{
            //console.log(body);
            return body;
        }).catch((reason => alert(reason)));
    }

    getCellInstances(){
        let url = this.baseUrl + "/build/cellInstances/";

        return fetch(url, {
            method: 'GET',
            headers: this.headers
        }).then(response => {
            return response.json();
        }).then(body =>{
            return body;
        }).catch((reason => alert(reason)));
    }

    getInputCells(){
        let url = this.baseUrl + "/build/inputCells/";

        return fetch(url, {
            method: 'GET',
            headers: this.headers
        }).then(response => {
            return response.json();
        }).then(body =>{
            return body;
        }).catch((reason => alert(reason)));
    }

    getConnections(){
        let url = this.baseUrl + "/build/connections/";

        return fetch(url, {
            method: 'GET',
            headers: this.headers
        }).then(response => {
            console.log(response);
            return response.json();
        }).then(body =>{
            console.log(body);
            return body;
        }).catch((reason => alert(reason)));
    }

    updateCellLocation(cell, x, y){
        let url = this.baseUrl + "/build/updateCellLocation/" + x + "/" + y + "/";

        return fetch(url, {
            method: 'PUT',
            headers: this.headers,
            body: JSON.stringify(cell)
        }).catch((reason => alert(reason)));
    }



    deleteCellInstance(id){
        let url = this.baseUrl + "/build/cellInstances/"+id;

        return fetch(url,{
            method: 'DELETE',
            headers: this.headers
        }).catch((reason => alert(reason)));
    }

    deleteConnection(id){
        let url = this.baseUrl + "/build/connections/"+id;

        return fetch(url,{
            method: 'DELETE',
            headers: this.headers
        }).catch((reason => alert(reason)));
    }
}